/* React */
body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(77, 77, 77);
    background-color: white;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
img {
    pointer-events: none;
}


.inp{
    width: 250px;
    height: 40px;
    text-align: center;
    border-radius: 15px;
    background-color: RGB(255, 197, 34);
    
    color: rgb(0, 0, 0);
    font-size: 20px;
    border: none;
}
.systemBtn{
    width: 200px;
    height: 50px;
    border: none;
    border-radius: 50px;
    background-color: rgb(56, 181, 215);
    color: white;
    font-size: 20px;
}

.head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    position: relative;
}
.logo{
    width: 250px;
    display: flex;
   
}

.logo-img{
    width: 180px;
}
.menu{
    font-size: 45px;
    color:rgb(77, 77, 77);
}
.menu-line-btn{
   width: 40px;
   height: 4px; 
   background-color: gray;
   border-radius: 15px;
}

/*scanner*/

.hand{
    position: absolute;
    animation: handanim 3s infinite;
}

@keyframes handanim{
    from {left: -30%;top: 40%}
    50% {left: 0%;top: 20%}
    to {left: -30%;top: 40%}
}

.radarImg{
    width: 200px;  
}
.fingerContainer{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.fingerScanner{
    width: 120px;
    height: 2px;
    /* background-color: rgb(82, 255, 82); */
    position: absolute;
    box-shadow: rgb(30, 255, 1) 1px 1px 20px 5px;
    border-radius: 50%;
    animation: scan 3s infinite;
}
@keyframes scan{
    from{top:0%;}
    50%{top: 100%;}
    to{top: 0%;}
}
.fingerID{
    width: 100px;
}

.Cyrcle{
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin: auto;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative; 
    z-index: 2;
}

.tickateIcon{
    width: 90px;
    animation: sakumSize 3s infinite;
    position: absolute;
    top: 15%;
}

@keyframes sakumSize {
    from {width: 80px;}
    50% {width: 120px;}
    to {width: 80px;}
}
.titleBuis{
    position: absolute;
    bottom: 10%;
}


.menuList{
    position: absolute;
    z-index: 3;
    width: 150px;
    border: 2px solid rgb(62, 62, 62);
    right: 2%;
    top: 5%;
    border-radius: 13px;
    background-color: white;
    overflow: hidden;
    box-shadow: gray 2px 2px 5px;
}
.menuOption{
    border-bottom: 1px solid rgb(62, 62, 62);
    font-size: 24px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.menuIcons{
    width: 30%;
}
.textOptionMenu{
    width: 70%;
    text-align: right;
}
.infoContainer{
    padding: 10px;
    color: white;
    font-size: 25px;
    text-shadow: black 2px 2px;
}

.infoTextDiv{
    background-color: white;
    padding: 15px;
    text-shadow: none;
    color: black;
    border-radius: 15px;
    margin-bottom: 25px;
}
.logo-by{
    width: 150px;
}

